'use client'
import React, { useState, useEffect } from 'react';
import styles from './products.module.css';
import Link from 'next/link';
import { useRouter } from 'next/router';

const Products = () => {
    const [productos, setProductos] = useState([]);
    const [error, setError] = useState('');
    const router = useRouter();
    const { categoria } = router.query;

    useEffect(() => {
        const fetchProducts = async () => {
            try {
                const response = await fetch('/data/products.json');
                if (response.ok) {
                    const data = await response.json();
                    setProductos(data.productos);
                } else {
                    throw new Error('No se pudo cargar los productos');
                }
            } catch (e) {
                setError('Error al cargar los productos');
                console.error(e);
            }
        };

        fetchProducts();
    }, []);

    const filteredProducts = categoria ?
        productos.filter(p => p.categoria.toLowerCase() === categoria.toLowerCase()) :
        productos;

    return (
        <div>
            <header className={styles.header}>
                <nav className={styles.navCenter}>
                    <img 
                        src="/images/logo2.png" 
                        alt="Logo de la joyerÃ­a" 
                        className={styles.logo} 
                        onClick={() => router.push('/')} 
                    />
                </nav>
            </header>
            <main>
                <div className={styles.productosGrid}>
                    {error ? (
                        <p>{error}</p>
                    ) : (
                        filteredProducts.map((producto) => (
                            <Link key={producto.id} href={`/productDetail?id=${producto.id}`} passHref>
                                <div className={styles.productoCard}>
                                    <img src={`/images/${producto.imagen}`} alt={producto.nombre} />
                                    <p className={styles.productoNombre}>{producto.nombre}</p>
                                    <p>Precio actual: {producto.subasta_actual}</p>
                                </div>
                            </Link>
                        ))
                    )}
                </div>
            </main>
        </div>
    );
};

export default Products;
